html {
  font-size: 120%;
}

body {
  font-family: 'Montserrat', sans-serif;
  background-color: #f2f2f2;
  margin: 0;
  padding: 0;
  color: #333;
}

.container {
  max-width: 1000px;
  margin: 0 auto;
  width: 90%;
}

#home-page-box {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.text-light {
  color: #999;
}


#title {
  font-size: 1.4rem;
}

.col-6 {
  width: 50%;
}

.row {
  width: 100%;
  display: flex;
}


#name > .underline {
  background-color: #ff6666;
  height: 9px;
  bottom: 16px;
  position: relative;
  z-index: 25;
}





#name > h1 {
  margin: 0;
  position: relative;
  z-index: 50;
  font-size: 2.4rem;
  font-weight: bold;
}

#connect-icons > a > svg{
  padding-right: 1em;
  color:#333;
  transition: color .2s;
}

#connect-icons > a > svg:hover {
  color: #ff6666;
}

#contact-me-link {
  color: #333;
  font-weight: 700;
  text-decoration: none;
  transition: color .2s;
  z-index: 50;
  position: relative;
}

#contact-me-underline {
  background-color: #ff6666;
  height: 4px;
  bottom: 7px;
  position: relative;
  z-index: 25;
  opacity: 0;
  transition: opacity .2s;
}

#contact-me-link:hover + #contact-me-underline  {
  opacity: 1;
}

#one:hover + #two {
  display: block;
}

.pb-1 {
  padding-bottom: 1em;
}

.pb-2 {
  padding-bottom: 2em;
}



@media screen and (max-width:480px) {
  html {
    font-size: 100%;
  }

  #name > .underline {
    height: 7px;
    bottom: 13px;
  }

  #contact-me-underline {
    height: 3px;
    bottom: 6px;
  }
}